import Letterize from 'letterizejs';
import anime from 'animejs/lib/anime.es.js';

// ÐÐ½Ð¸Ð¼Ð°ÑÐ¸Ñ ÑÐµÐºÑÑÐ°
const test = new Letterize({
  targets: '.animate-me',
});
const animation = anime.timeline({
  targets: test.listAll,
  delay: anime.stagger(100, {
    grid: [test.list[0].length, test.list.length],
    from: 'center',
  }),
  loop: true,
});

animation
  .add({
    scale: 0.5,
  })
  .add({
    letterSpacing: '10px',
  })
  .add({
    scale: 1,
  })
  .add({
    letterSpacing: '6px',
  });

// ======== ÐÐ½Ð¸Ð¼Ð°ÑÐ¸Ñ ÑÐ»Ð¸ÑÐºÐ¸ =============
let design = anime({
  targets: 'svg #XMLID5',
  keyframes: [
    { translateX: -1200 },
    { rotateY: 180 },
    { translateX: 1500 },
    { rotateY: 0 },
    { translateX: -1200 },
    { rotateY: 180 },
    { translateX: 300 },
  ],
  easing: 'easeInOutSine',
  duration: 60000,
});
